
import { Country } from "../../country-category/masters/country";
import { Department } from "../../department/masters/department";
import { LookupEntity } from "../../lookup-module/masters/lookup-entity";
import { BaseEntity } from "../../masters/base-entity";
import { Branch } from "../../service-center/masters/branch";
import { EmployeeContactDetails } from "./EmployeeContactDetails";
import { EmployeeDependent } from "./employeeDependent";
import { EmployeeQualification } from "./employeeQualification";
import { Employment } from "./Employment";
import { EnterpriseUser } from "./enterpriseUser";
import { PrefixMaster } from "./PrefixMaster";

export class Employee extends BaseEntity{
	employeeId : number;
	employeeNo : string;
	firstName: string;
	middleName: string;
	lastName: string;
	aliasName: string;
	fatherName: string;
	dob: Date  = new Date();
	employeeStatus :LookupEntity  = new LookupEntity;
	gender :LookupEntity  = new LookupEntity;
	maritalStatus :LookupEntity  = new LookupEntity();
	empNationality :LookupEntity  = new LookupEntity();
	nationality : Country = new Country;
	empCategory :LookupEntity  = new LookupEntity();
	empEmployeementStatus :LookupEntity  = new LookupEntity();
	department :Department  = new Department();
	bloodGroup :LookupEntity  = new LookupEntity();
	empHealthCardValidUpto : Date = new Date;
	empDesignation : LookupEntity  = new LookupEntity();
	empdependent: Array<EmployeeDependent>;
	empContactDetails: Array<EmployeeContactDetails>;
	employment: Array<Employment>;
	empqualification: Array<EmployeeQualification>;
	mobileNo: string;
	user: EnterpriseUser  = new EnterpriseUser;
	branch : Branch = new Branch();
	employeeFullName : string;
	employeeStatusId : number;
	qualification: string;
	speciality: string;
	phoneNo: string;
	email: string;
	validDate: Date = new Date;
	reportingAuthority : number;
	empPrefix: PrefixMaster  = new PrefixMaster();
	fullNameWithPrefix: string;
	employeeName : string;
	licenseNo: string;
	loginId : string;
	password: string;
	restrictedEmp: boolean;
	siteId: number;
	discPercentage : number;
	newPatient: number;
	existingPatient: number;
	followupPatient: number;
	slot: LookupEntity = new LookupEntity;
	
	
}