import { Injectable } from '@angular/core';
import { EnterpriseUser } from '../enterprise-user/masters/enterprise-user';
import { User } from '../login/master/user';
import { Patient } from '../patient/master/patient';
import { Preferences } from '../preferences/masters/preferences';

@Injectable({
  providedIn: 'root'
})
export class CommonSecurityService
 {
	
  constructor() { }
  
  /************ Patient services goes here **************************************************** */
	getPatient():any{
		return JSON.parse(localStorage.getItem('patient'))
	}
	setPatient(patient:Patient){
		localStorage.setItem('patient',JSON.stringify(patient));
	}
	public cleanPatient(){
		localStorage.removeItem('patient');
	}
	
	/****************************************  Preferences Services Goes here **************** */
	
	public getPreferences():any{
		return JSON.parse(localStorage.getItem('preferences'));
	}
	public setPreferences(preferences: Preferences){
		if(preferences){
			localStorage.setItem('preferences',JSON.stringify(preferences));			
		}
	}
	public cleanPreferences(){
		localStorage.removeItem('preferences');
	}
	
	/*******************************************  User Services Goes here ************************************/
	public setUser(user:EnterpriseUser){
		if(user){
			localStorage.setItem('user',JSON.stringify(user));
		}
	}
	public getUser():any{
		return JSON.parse(localStorage.getItem('user'));
	}
	public cleanUser(){
		localStorage.removeItem('user');
	}
}
